@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@1,300;1,700&display=swap);
*{margin:0;padding:0}html,body{height:100vh}.App{font-family:IBM Plex Mono,monospace;text-align:center;margin:0;padding:0;width:100%;height:100vh;background:rgba(255,187,138,0.7)}.control{position:relative;height:40vh;cursor:pointer;display:flex;flex-flow:row wrap;align-items:left}.control:hover .circle-click{background:#deff8a}.control .prompt img{height:40vh;padding:10px 25px;text-align:left}.control .status,.control .status-talk{bottom:0;padding-left:50px;padding-right:20px;width:100vw;font-family:IBM Plex Mono, monospace;font-style:italic;font-weight:light;font-size:20pt;text-align:left;color:#063746;z-index:1}.control .status .status-text,.control .status-talk .status-text{z-index:5;position:relative;white-space:normal}.control .circle,.control .circle-click,.control .circle-speaking,.control .circle-recognising{position:absolute;width:80px;height:80px;left:-40px;border-radius:50px;cursor:pointer;border:0;background-color:#CD1515;z-index:0}.control .circle-click{border:0px;background-color:#fff}.control .circle-speaking{border:0px;background-color:#fff;-webkit-animation:bordering 2s infinite;animation:bordering 2s infinite}.control .circle-recognising{-webkit-animation:bordersize 2s infinite;animation:bordersize 2s infinite}.select-wrapper{margin-left:40px;margin-right:20px;top:35vh;position:absolute}.select{list-style-type:none;padding:0;margin:0;display:flex;flex-flow:row wrap;align-items:space-around;justify-content:flex-start}.flex{margin:0.5em;padding:1em;flex-basis:20vw;cursor:pointer;border-radius:5px;background:#fff;border:2px solid #063746}.flex:hover{background:#deff8a}img{object-fit:contain;width:100%;border-radius:5px}@-webkit-keyframes glowing{0%{background-position:0 0}50%{background-position:400% 0}100%{background-position:0 0}}@keyframes glowing{0%{background-position:0 0}50%{background-position:400% 0}100%{background-position:0 0}}@-webkit-keyframes bordering{0%{box-shadow:0 0 0px rgba(222,255,138,0.5)}50%{box-shadow:0 0 0px 8vh rgba(222,255,138,0.9)}100%{box-shadow:0 0 0px rgba(222,255,138,0.5)}}@keyframes bordering{0%{box-shadow:0 0 0px rgba(222,255,138,0.5)}50%{box-shadow:0 0 0px 8vh rgba(222,255,138,0.9)}100%{box-shadow:0 0 0px rgba(222,255,138,0.5)}}@-webkit-keyframes bordersize{0%{box-shadow:0 0 0px rgba(255,255,255,0.5)}50%{box-shadow:0 0 0px 8vh rgba(255,255,255,0.9)}100%{box-shadow:0 0 0px rgba(255,255,255,0.5)}}@keyframes bordersize{0%{box-shadow:0 0 0px rgba(255,255,255,0.5)}50%{box-shadow:0 0 0px 8vh rgba(255,255,255,0.9)}100%{box-shadow:0 0 0px rgba(255,255,255,0.5)}}[data-xstate]{justify-self:stretch;align-self:stretch;border:none}

